* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html,
body {
    font-family: "Merriweather", Helvetica, Arial, sans-serif;
}

.intro {
    width: 100%;
    background: #af1212;
    max-height: 804px;
}

.container {
    width: 1200px;
    margin: 0 auto;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 50px 10px 0 10px;
    &__menu-list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        list-style: none;
    }
    &__menu-item {
        margin-right: 30px;
    }
}
